var DayMath = require('./DayMath');

var HebrewTimes = require('./HebrewTimes');

var is = {
  SHABBAT: 'SHABBAT',
  NOT_SHABBAT: 'NOT_SHABBAT',
  CANDLELIGHTING: 'CANDLELIGHTING'
};

function isItShabbat(now, latitude, longitude) {
  // SATURDAY
  var countDownTo;
  var period;

  if (now.weekday === DayMath.ofWeek.Saturday) {
    var havdalaTime = HebrewTimes.havdala(now, latitude, longitude);

    if (now < havdalaTime) {
      period = is.SHABBAT;
      countDownTo = havdalaTime;
    } else {
      period = is.NOT_SHABBAT;
      countDownTo = HebrewTimes.candleLighting(now, latitude, longitude);
    } // FRIDAY

  } else if (now.weekday === DayMath.ofWeek.Friday) {
    var fridaySunsetTime = HebrewTimes.fridaySunset(now, latitude, longitude);
    var candleLightingTime = HebrewTimes.candleLighting(now, latitude, longitude);

    if (now >= candleLightingTime) {
      if (now < fridaySunsetTime) {
        period = is.CANDLELIGHTING;
        countDownTo = fridaySunsetTime;
      } else {
        period = is.SHABBAT;
        countDownTo = HebrewTimes.havdala(now, latitude, longitude);
      }
    } else {
      period = is.NOT_SHABBAT;
      countDownTo = candleLightingTime;
    } // OTHER DAYS

  } else {
    period = is.NOT_SHABBAT;
    countDownTo = HebrewTimes.candleLighting(now, latitude, longitude);
  }

  return {
    period: period,
    countDownTo: countDownTo
  };
}

module.exports = {
  is: is,
  isItShabbat: isItShabbat
};